.logo{
    cursor: pointer;
}
.joinBody {
    min-width: 290px;
    max-width: 460px;
    padding: 0 4.35%;
    text-align: center;
}
.joinBodyMain{
    border: 1px;
    border-color: black;
    border-style: solid;
}
.joinSize{
    display: block;
    position: relative;
    height: 48px;
    padding: 0;
    border: 1px solid #ccc;
    background-color: #fff;
    transition: border-color .25s ease;
}
.joinSizeSpace{
    margin-bottom: 2.9%;
}
.joinImgDiv{
    position: relative;
    float: left;
    display: inline;
    min-width: 44px;
    _width: 44px;
    height: 100%;
    border-right: 1px solid #ccc;
    background-color: #fafafa;
    cursor: pointer;
}
.joinInputDiv{
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
}
.joinInput{
    width: 100%;
    padding: 16px 0 12px;
    border: 0 none;
    background: none transparent;
    font-family: dotum,
        sans-serif;
    font-size: 14px;
    line-height: 20px;
    color: #111;
    font-weight: 700;
    text-indent: 12px;
}
.joinImg{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    margin: -8px 0 0 -10px;
    padding-left: 1px;
}
.joinButton{
    width: 100%;
    padding: 16px 0 12px;
    border: 0 none;
    background: none transparent;
    font-family: dotum,
        sans-serif;
    font-size: 14px;
    line-height: 20px;
    color: #111;
    font-weight: 700;
    cursor: pointer;
}             
.joinGender{
    display: flex;
    margin-bottom: 2.9%;    
}
.joinGenderMan{
    background-color: gray;
}
.joinGenderWoman{
    background-color: gray;
}
.wrapper {
    width: auto;
    margin: 0 auto;
}
.borderLine{
    border: 1px;
    border-color: black;
    border-style: solid;
}
